

























import { Component, Vue } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import HeaderLogin from '../components/HeaderLogin.vue'
import { trim } from 'lodash'

const beAuth = namespace('beAuth')

@Component({
  components: {
    HeaderLogin
  }
})
export default class LoginPage extends Vue {
  @beAuth.Action('login') login!: (data: { username: string; password: string; otp?: string }) => Promise<any>

  userData = {
    username: '',
    password: ''
  }
  loading = false
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  validateLoginForm = (rule?: any, value?: any, callback?: any) => {
    if (rule.field === 'username' && trim(this.userData.username) === '') {
      callback(new Error('Tên đăng nhập không được để trống'))
    }
    if (rule.field === 'password' && trim(this.userData.password) === '') {
      callback(new Error('Mật khẩu không được để trống'))
    }
    callback()
  }

  loginRules = {
    username: [{ validator: this.validateLoginForm, trigger: 'blur' }],
    password: [{ validator: this.validateLoginForm, trigger: 'blur' }]
  }

  async onSubmit(): Promise<void> {
    try {
      if (this.userData.username !== '' && this.userData.password !== '') {
        this.loading = true
        await this.login(this.userData)
        this.$router.push({ name: 'home' })
        this.loading = false
      }
    } catch (error) {
      this.loading = false
      if (error.errorCode >= 500) {
        this.$message.error('Có lỗi kết nối tới máy chủ. Vui lòng thử lại sau ít phút')
      }
      console.log(error)
    }
  }
}
