











































import { Component, Vue } from 'vue-property-decorator'
import { CompanyInfoEntity } from '@/services/entities/companyInfo'
import getRepository from '@/services'
const beCompanyInfo = getRepository('company')
@Component({ components: {} })
export default class HeaderLogin extends Vue {
  isOpen = false
  companyInfo: CompanyInfoEntity = {}

  // lifecycle
  mounted(): void {
    beCompanyInfo.getInfo().then((res: CompanyInfoEntity) => {
      this.companyInfo = res
    })
  }

  //action

  visibleChange(): void {
    this.isOpen = !this.isOpen
  }

  handleCommand(command: string): void {
    if (command === 'chplay' && this.companyInfo['app.link.android']) {
      window.open(this.companyInfo['app.link.android'], '_blank')
    }
    if (command === 'appstore' && this.companyInfo['app.link.ios']) {
      window.open(this.companyInfo['app.link.ios'], '_blank')
    }
    // switch (command) {
    //   case 'chplay':
    //     window.open(this.companyInfo['app.link.android'], this.companyInfo['app.link.android'] ? '_blank' : '')
    //     break
    //   case 'appstore':
    //     window.open(this.companyInfo['app.link.ios'], '_blank')
    //     break
    //   case 'macos':
    //     window.open(this.companyInfo['app.link.macos'], '_blank')
    //     break
    //   case 'window':
    //     window.open(this.companyInfo['app.link.window'], '_blank')
    //     break
    // }
  }
}
